</template>

<script>
import { follow, unfollow } from "@jx3box/jx3box-common-ui/service/follow";
import { __userLevelColor,__imgPath } from "@jx3box/jx3box-common/data/jx3box";
import { user as medal_map } from "@jx3box/jx3box-common/data/medals.json";
import frames from "@jx3box/jx3box-common/data/user_avatar_frame.json";
import User from "@jx3box/jx3box-common/js/user";
import { getFansCount } from "@jx3box/jx3box-common-ui/service/follow";
import {tvLink} from "@jx3box/jx3box-common/js/utils";
import dateFormat from "@/utils/dateFormat";
import {deny,undeny} from "@/service/author";
import Left from "./Left"
import  Primary from './Primary';
export default {
    name: "Me",
    components:{
        Left,
        Primary
    },
    data: function () {
        return {
            medals: [],
            medal_map,
            frames,
            isVIP: false,
            isFollow:false,
            attentionText:'已关注',
            moreOperate:false,
            moreOperatePhone:false,
            fansNum:0,
            authorInfo:{},
            avatarSize:'l',
            fansLink:'/dashboard/privacy?tab=myfans',
            userDefinedStyle:{
                fans:{},
                btn:{},
                userName:{},
                banner:{}
            }
        };
    },
    computed: {
        uid: function () {
            return this.$store.state.uid;
        },
        data: function () {
            return this.$store.state.userdata;
        },
        avatar: function () {
            return this.data.user_avatar || "";
        },
        avatar_frame: function () {
            return this.data.user_avatar_frame || "";
        },
        isPRO: function () {
            return this.data?.is_pro;
        },
        isSuperAuthor: function () {
            return !!this.data?.sign;
        },
        vipType: function () {
            return this.isPRO ? "PRO" : "PRE";
        },
        vipTypeTitle: function () {
            return this.isPRO ? "专业版会员用户" : "高级版会员用户";
        },
        level: function () {
            return User.getLevel(this.data?.experience || 0);
        },
        tv_link: function () {
            return tvLink(this.data.tv_type, this.data.tv_id) || "";
        },
        tv_img: function () {
            return __imgPath + "image/tv/" + this.data.tv_type + ".png";
        },
        frameUrl: function () {
            return __imgPath + `image/avatar/${this.avatar_frame}/${this.avatar_frame}.svg`;
        },
        isLogin: function () {
            return User.isLogin();
        },
        isSelf:function(){
            return User.getInfo().uid == this.uid
        }
    },
    filters:{
        time: (val) => {
            return dateFormat(new Date(val));
        },
    },
    methods: {
        copyData(text){
            let _this=this
            this.$copyText(String(text)).then(function (e) {
                _this.$notify({
                    title: '复制成功',
                    message: '复制内容：' + text,
                    type: 'success'
                });
            }, function (e) {
                _this.$notify({
                    title: '复制失败',
                    message: '请手动复制',
                    type: 'warning'
                });
            })
        },
        getDecorationJson(){
            let decoration_me=sessionStorage.getItem('decoration_me'+this.uid)
            if(!decoration_me){
                let num=0
                let intervalTimerId = setInterval(() => {
                    decoration_me=sessionStorage.getItem('decoration_me'+this.uid)
                    num++
                    if(decoration_me || num>10){
                        clearInterval(intervalTimerId)
                        this.setDecorationStyle(decoration_me)
                    }
                }, 100);
            }else{
                this.setDecorationStyle(decoration_me)
            }
        },
        showDecoration:function(val,type){
            return __imgPath + `decoration/images/${val}/${type}.png`;
        },
        setDefaultBanner(){
            this.userDefinedStyle.banner=`url(https://img.jx3box.com/decoration/images/0_TESTSAMPLE/homebanner.png)`
        },
        setDecorationStyle(decoration_me){
            if(decoration_me == 'no'){
                this.setDefaultBanner()
                return;
            }
            let decoration=JSON.parse(decoration_me)
            if(decoration.highlightcolor){
                this.userDefinedStyle.fans={
                    'color':decoration.highlightcolor,
                    'fill':decoration.highlightcolor
                }
            }
            if(decoration.textcolor){
                this.userDefinedStyle.userName={
                    'color':decoration.textcolor
                }
            }
            if(decoration.buttoncolor){
                this.userDefinedStyle.btn={
                    'background-color':decoration.buttoncolor,
                    'fill':decoration.buttontextcolor,
                }
            }
            this.userDefinedStyle.banner='url('+this.showDecoration(decoration.name,"homebanner")
        },
        // 关注
        follow() {
            if (!this.isLogin) {
                User.toLogin();
                return;
            }
            follow(this.uid)
                .then((res) => {
                    this.$message.success("关注成功");
                    this.isFollow = true;
                    this.loadFans()
                })
                .catch((err) => {
                    console.log(err);
                });
        },
        // 取消关注
        unfollow() {
            this.$confirm("确定不再关注此人？", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning",
            }).then(() => {
                unfollow(this.uid)
                    .then((res) => {
                        this.$message.success("取关成功");
                        this.isFollow = false;
                        this.loadFans();
                    })
                    .catch((err) => {
                        console.log(err);
                    });
            }).catch(_ => {});;
        },
        showLevelColor : function (level){
            return __userLevelColor[level]
        },
        // 格式化粉丝数
        formatFansNum(num) {
            if (num < 10000) {
                return num === 0 ? "" : num;
            } else {
                return (num / 10000).toFixed(1) + "万";
            }
        },
        loadFans(){
            getFansCount(this.$store.state.uid).then(res=>{
                this.fansNum = res.data.data.follower_count || 0;
                this.isFollow = res.data.data.is_followed;
            })
        },
        getAuthorInfo(v){
            this.authorInfo=v
        },
        //拉黑
        joinBlacklist(){
            this.$confirm("确定要拉黑此人？", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning",
            }).then(() => {
                deny(this.uid).then(() => {
                    this.$message.success("拉黑成功");
                }) .catch((err) => {
                    console.log(err);
                });
            }).catch(_ => {});;

        },
        avatarSizeChange(){
            let w=document.body.clientWidth || document.documentElement.clientWidth
            if(w>720){
                this.avatarSize='l'
            }else if(w>500){
                this.avatarSize='m'
            }else{
                this.avatarSize='s'
            }
        },
        // 跳转粉丝界面
        toFans(){
            if(this.isSelf){
                window.open(this.fansLink,'_blank')
            }
        }
    },
    created() {
        this.avatarSizeChange()
       this.getDecorationJson()
    },
    mounted: function () {
        if (this.uid) {
            this.loadFans();
        }
        let _this=this
        window.onresize = function () {
            _this.avatarSizeChange();
        }
    },
}
</script>

<style scoped lang="less">
@import "../../assets/css/newCss/me.less";
@import "../../assets/css/newCss/primary.less";
